import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import HotSpot from "../../Elements/HotSpot";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'
import { showHotspot } from "../../../../../../../../../../actions/themeActions/themeActions";
import { connect } from "react-redux";
class Image extends PureComponent {
  componentDidMount(){
    marginTop_mainDiv()
  }





  render() {
    const { ImageClass, ImageUrl, item, ugc_products, wall,width, height, showHotspot} = this.props
    const styleImg = { height: item.hotspot ? `100%` : `auto` , width: item.hotspot ? `100%` : `100%`, margin: `0px auto`   }
    const imageStyle = {
      paddingBottom: height == "" ? height+'%' : height  + '%', width: '100%'
    };

    return (
      <div  className={ImageClass} style={imageStyle} onMouseEnter={() =>showHotspot({showAll: true})} onMouseLeave={() =>showHotspot({showAll: false})} >
        
         <img loading="lazy" src={ImageUrl} data-src={ImageUrl} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} style={styleImg} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} height={height} width={width} className={`imgID${item.id}`} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} alt="image" />
         {item.hotspot ?<HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item} /> :null }
      </div>
    );
  }
}

const mapStateToProps = state => ({
})
const mapDispatchToProps = dispatch => {
  return {
   
   showHotspot: (data) => dispatch(showHotspot(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Image);