import React, { PureComponent } from "react";
import {convertHtmlStringToRender} from '../../../Components/customFunction'
import { isValidAuthorImage } from '../../../../utils';
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions';
import Time from "../../Elements/Time";
import AuthorAvatar from '../../Elements/AuthorAvatar'

const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;

export default class AuthorDetails extends PureComponent {
  state = { isAuthorImageValid: true }
  componentDidMount() {
    const { item } = this.props;
    const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
    this.setState({ isAuthorImagevalid: isAuthorImagevalid });
  }
  onCtaClick = event => {
    const { wall, item } = this.props;
    themePostTracking({ type: 1, action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId, })
  }

  render() {
    const { wall, item, network } = this.props;
    const { isAuthorImagevalid } = this.state; const postTime = wall.Personalization.postTime; 
    const isCta = checkStringInNotNull(item.cta); const postAuthor = wall.Personalization.postAuthor;
    const networkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor; 
    const BGImage = { backgroundImage: `url(${item.author.picture})`};
    const tb_authorBgColor = { backgroundColor: wall.ThemeRule.authorColor };
    const tb_authorColor = { color: wall.ThemeRule.authorColor };
    const networkIcon = network.icon.replace('fa-', '');
    

    return (<>
      {isCta ? <span onClick={this.onCtaClick}> {convertHtmlStringToRender(item.cta)}</span> : null}
      <div className="tb_sc_author_wrapper">
        {!item.author.isInstaUser ?
          <div className="tb_sc_author">
            <div className="tb_sc_author_profile"> 
                        <img loading="lazy" src={item.author.picture} onError={(e)=>{e.target.onerror = null;  e.target.src=item.author.errorPic}}  height={44} width={44} alt="image"/> </div> 
            <div className="tb_sc_author_info"> 
            {postAuthor ? <div className="tb_sc_authorname"  style={tb_authorColor}>{item.author.name}</div>:''}
              <div className="tb_sc_post_info">
                 {postAuthor ? <div className="tb_sc_username" style={tb_authorColor}>@{item.author.username}</div>:''}
                 {postTime && postAuthor ? <div className="tb_sc_seprator" style={tb_authorBgColor}><div></div></div>:''}
                 {postTime ? <Time postTime={item.createdAt} timeClass={'tb_sc_time'} timeStyle={tb_authorColor}/> :''}
              </div>
            </div>
          </div> : null}
        <div className="tb_sc_social_">
          <div className={`tb__icon tb-${networkIcon}`} style={{ color: networkColor }}><div></div></div>
        </div>
      </div>
    </>
    );
  }
}
